import React from 'react'
import PropTypes from 'prop-types'
import { Grid, withStyles } from '@material-ui/core'

import sections from '../../content/sections'

import AppBar from '@material-ui/core/AppBar'
import Navigation from '../navigation/navigation'

// TODO: check for base styling
const styles = theme => ({
  root: {
    backgroundColor: '#313131',
    padding: theme.spacing(4),
    direction: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    textAlign: 'center',
    [theme.breakpoints.up('md')]: {
      position: 'fixed',
    },
    [theme.breakpoints.down('md')]: {
      padding: theme.spacing(2),
    },
  },
  item: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0),
    margin: theme.spacing(0),
    [theme.breakpoints.down('md')]: {
      textAlign: 'left',
      alignItems: 'left',
    },
  },
  link: {
    height: '100%',
    padding: theme.spacing(0),
    margin: theme.spacing(0),
  },
  image: {
    minWidth: 116,
    minHeight: 30,
    padding: theme.spacing(1),
    margin: theme.spacing(0),
    [theme.breakpoints.down('md')]: {
      padding: theme.spacing(1),
    },
  },
  offset: {
    ...theme.mixins.toolbar,
    flexGrow: 1,
    [theme.breakpoints.down('md')]: {
      padding: theme.spacing(15),
    },
  },
})

const Header = ({ siteTitle, classes }) => {
  const smallLogoUrl = '/compose-us-white-small.png'

  const navCallBack = param => {
    window.location = '/#' + param
  }

  return (
    <React.Fragment>
      <AppBar position="sticky" className={classes.root}>
        <Grid container justify={'space-between'}>
          <Grid item className={classes.item}>
            <a
              href="/#"
              className={classes.link}
              aria-label={'compose.us logo'}
            >
              <img
                src={smallLogoUrl}
                className={classes.image}
                alt="compose.us logo"
              />
            </a>
          </Grid>
          <Grid item className={classes.item}>
            <Navigation
              sections={sections}
              callback={param => navCallBack(param)}
              aria-label={`Navigation for: ${siteTitle}`}
            />
          </Grid>
        </Grid>
      </AppBar>
    </React.Fragment>
  )
}

Header.propTypes = {
  classes: PropTypes.object.isRequired,
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default withStyles(styles)(Header)
